.contactWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1rem;
  gap: 1rem;
}
.contactMeTitle {
  font-size: 2rem;
  color: rgb(141, 139, 139);
}
.desc {
  font-size: 1.2rem;
}

.contactMedia {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}

.image {
  border-radius: 50%;
}

.contactitle {
  font-size: 1rem;
  font-weight: 700;
  color: red;
}
.contentData {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 1rem;
}

.description {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

}
